import React, { Component } from 'react';
import FormActionBar from '../common/FormActionBar';

class QuestionnaireResultDetail extends Component {

    constructor(props) {
        super(props);
        this.state  =   {
            questionnaireDetail     :   {}
        };
    }

    componentWillMount() {

        var questionnaireKey      =   this.props.match.params.questionnaireKey;

        var questionnaireResult   =   localStorage.getItem(questionnaireKey);
            questionnaireResult   =   JSON.parse(questionnaireResult);

        console.log("questionnaireResult", questionnaireResult);

        this.setState({
            questionnaireResult   :   questionnaireResult
        });
    }

    saveForm() {

        var questionnaireDetail = this.state.questionnaireDetail;

        var storedKey       =   "questionnaire_result_" + questionnaireDetail.topic;

        var storedDetail  =   localStorage.getItem(storedKey);

        if(!storedDetail) {
            storedDetail  =   [];
        }
        else {
            storedDetail  =   JSON.parse(storedDetail);
        }

        storedDetail.push(questionnaireDetail);

        //
        // storedDetail.push({
        //     topic   :   this.state.questionnaireTopic,
        //     form    :   list
        // });

        localStorage.setItem(storedKey, JSON.stringify(storedDetail));

        console.log("saveForm::localStorage", localStorage.getItem(storedKey));

        this.props.history.push("/questionnaires/list");

    }

    handleChange(listIndex, field, value) {

        console.log("handleChange");

        var questionnaireDetail  =   this.state.questionnaireDetail;

        questionnaireDetail.form[listIndex][field]  =   value;

        console.log("questionnaireDetail", questionnaireDetail);

        this.setState({
            questionnaireDetail    :   questionnaireDetail
        });
    }

    render() {
        return (
            <div className="app-pages app-section">
                <div className="container">

                    <h4>Topic: {this.state.questionnaireResult[0].topic}</h4>

                    {
                        this.state.questionnaireResult.map(function(listElement, listIndex){
                            return (
                                <div className="product-cart questionnaire-list-item">
                                    <div className="entry">
                                        <div className="cart-title">

                                            <h5>Result #{listIndex + 1}</h5>

                                            {
                                                listElement.form.map(function(formElement, elementIndex){
                                                    return (
                                                        <div>
                                                            <div className="row row-title">
                                                                <div className="col s12">
                                                                    <h5 className="text-label">{elementIndex + 1}. {formElement.title}</h5>
                                                                </div>
                                                            </div>

                                                            <div className="row row-title">
                                                                <div className="col s12">
                                                                    <label className="text-label">{formElement.textAnswer}</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    )
                                                })
                                            }

                                        </div>
                                    </div>
                                </div>
                            )
                        }.bind(this))
                    }

                </div>
            </div>
        );
    }
}

export default QuestionnaireResultDetail;



