import React, { Component } from 'react';
import FormActionBar from '../common/FormActionBar';

class QuestionnaireGeneratorApp extends Component {

    constructor(props) {
        super(props);
        this.state  =   {
            questionnaireTopic  :   "",
            formElementList     :   []
        };
    }

    saveForm() {

        var list = this.state.formElementList;

        var storedList  =   localStorage.getItem("questionnaireFormList");

        if(!storedList) {
            storedList  =   [];
        }
        else {
            storedList  =   JSON.parse(storedList);
        }

        storedList.push({
            topic   :   this.state.questionnaireTopic,
            form    :   list
        });

        localStorage.setItem("questionnaireFormList", JSON.stringify(storedList));

        console.log("saveForm::localStorage", localStorage.getItem("questionnaireFormList"));

        this.props.history.push("/questionnaires/list");

    }

    handleTopicChange(value) {
        console.log(value);
        this.setState({
            questionnaireTopic  :   value
        });
    }

    handleChange(listIndex, field, value) {
        var list = this.state.formElementList;
            list[listIndex][field]  =   value;

        if(field == "type") {
            list[listIndex].options  =   [];
        }

        this.setState({
            list    :   list
        });
    }

    handleOptionChange(listIndex, optionIndex, field, value) {

        var list = this.state.formElementList;
            list[listIndex].options[optionIndex][field]  =   value;

        this.setState({
            list    :   list
        });
    }

    deleteListItem(listIndex) {
        var list = this.state.formElementList;
            list.splice(listIndex, 1);

        this.setState({
            list    :   list
        });
    }

    addFormElement() {
        var list = this.state.formElementList;

        list.push({
            title       :   "",
            type        :   "",
            options     :   [],
            textAnswer  :   ""
        });

        this.setState({
            formElementList: list
        })
    }

    addOption(listIndex) {
        var list = this.state.formElementList;

        list[listIndex].options.push({
            text    :   ""
        });

        this.setState({
            formElementList: list
        })
    }

    render() {
        return (
            <div className="app-pages app-section">
                <div className="container" style={{marginBottom: 40 + "px"}}>

                    <h4>Questionnaire Generator</h4>

                    <input type="text" value={this.state.questionnaireTopic} placeholder="topic" onChange={(e) => this.handleTopicChange(e.target.value)} />

                    {
                        this.state.formElementList.map(function(listElement, listIndex){
                            return (
                                <div className="product-cart questionnaire-list-item">
                                    <div className="entry">
                                        <div className="cart-title">
                                            <div className="row">
                                                <div className="col s11">
                                                    <h6>Question #{listIndex + 1}</h6>
                                                </div>
                                                <div className="col s1">
                                                    <a href="#" onClick={(e) => this.deleteListItem(listIndex)}><i className="fa fa-remove"></i></a>
                                                </div>
                                            </div>

                                            <div className="row row-title">
                                                <div className="col s12">
                                                    <input type="text" value={listElement.title} placeholder="title" onChange={(e) => this.handleChange(listIndex, "title", e.target.value)} />
                                                </div>
                                            </div>

                                            <div className="row row-type">
                                                <div className="col s4 col-label">
                                                    <h6>Answer Type</h6>
                                                </div>
                                                <div className="col s8">
                                                    <select value={listElement.type} onChange={(e) => this.handleChange(listIndex, "type", e.target.value)}>
                                                        <option value="text">Text</option>
                                                        <option value="radio">Radio Button</option>
                                                    </select>
                                                </div>
                                            </div>
                                            {
                                                (listElement.type == "radio") ? (
                                                    <div>
                                                        <table>
                                                        {
                                                            listElement.options.map(function (optionText, optionIndex) {
                                                                return (
                                                                    <tr>
                                                                        <td>
                                                                            <input type="radio" name="gender"
                                                                                   value="male"/><label>&nbsp;</label>
                                                                        </td>
                                                                        <td>
                                                                            <input type="text" placeholder="Option"
                                                                                   value={optionText.text}
                                                                                   onChange={(e) => this.handleOptionChange(listIndex, optionIndex, "text", e.target.value)}/>
                                                                        </td>
                                                                    </tr>
                                                                )
                                                            }.bind(this))
                                                        }
                                                        </table>

                                                        <div className="row row-type">
                                                            <div className="col s12">
                                                                <i className="fa fa-plus-circle button-add-option fa-2x" aria-hidden="true" onClick={(e) => this.addOption(listIndex)}></i>
                                                                <label>Add Option</label>
                                                            </div>
                                                        </div>
                                                    </div>
                                                ) : false
                                            }

                                        </div>
                                    </div>
                                    {
                                        /*
                                         <input type="text" placeholder="Email" />
                                         <select>
                                         <option>Text</option>
                                         <option>Radio Button</option>
                                         </select>
                                         */
                                    }
                                </div>
                            )
                        }.bind(this))
                    }

                    <div className="button-add-list-item" onClick={() => this.addFormElement()}>
                        <i className="fa fa-plus-circle" aria-hidden="true"></i>
                        <h5>Add Elements</h5>
                    </div>

                    {/*
                    <div className="pages-title">
                        <h3>Login</h3>
                    </div>
                    <form action="#">
                        <input type="email" placeholder="Email" />
                        <input type="password" placeholder="password" />
                        <div><a href="" className="forgot">Forgot Password?</a></div>
                        <div className="chebox">
                            <input type="checkbox" id="checkbox" />
                            <label for="checkbox">Remember me</label>
                        </div>
                        <button className="button">Login</button>
                        <div className="create-account">Not Registered? <a href="">Create an account</a></div>
                    </form>
                    <div className="or">
                        <h5>OR</h5>
                        <button className="button facebook">Login with Facebook</button>
                        <button className="button twitter">Login with Twitter</button>
                        <button className="button google">Login with Google+</button>
                    </div>
                    */}
                </div>
                <FormActionBar saveForm={this.saveForm.bind(this)} label="Create Questionnaire" />
            </div>
        );
    }
}

export default QuestionnaireGeneratorApp;



