import React, { Component } from 'react';
import FormActionBar from '../common/FormActionBar';

class QuestionnaireList extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        var questionnaireList   =   localStorage.getItem("questionnaireFormList");

        if(!questionnaireList) {
            questionnaireList   =   [];
        }
        else{
            questionnaireList   =   JSON.parse(questionnaireList);
        }

        console.log("questionnaireList", questionnaireList);

        this.setState({
            questionnaireList   :   questionnaireList
        });
    }

    deleteQuestionnaire(questionnaireIndex) {
        var questionnaireList   =   this.state.questionnaireList;
            questionnaireList.splice(questionnaireIndex, 1);

        localStorage.setItem("questionnaireFormList", JSON.stringify(questionnaireList));
    }

    viewDetail(questionnaireIndex) {
        this.props.history.push("/questionnaires/detail/" + questionnaireIndex);
    }

    render() {
        return (
            <div className="app-pages app-section">
                <div className="container" style={{marginBottom: 40 + "px"}}>

                    <h4>Your Questionnaire List</h4>

                    {
                        this.state.questionnaireList.map(function(listElement, questionnaireIndex){
                            return (
                                <div className="product-cart questionnaire-list-item">
                                    <div className="entry">
                                        <div className="cart-title">
                                            <div className="row">
                                                <div className="col s11" onClick={(e) => this.viewDetail(questionnaireIndex)}>
                                                    <h5>Questionnaire #{questionnaireIndex + 1}</h5>
                                                </div>
                                                <div className="col s1">
                                                    <a href="#" onClick={(e) => this.deleteQuestionnaire(questionnaireIndex)}><i className="fa fa-remove"></i></a>
                                                </div>
                                            </div>
                                            <div className="row">
                                                <div className="col s12" onClick={(e) => this.viewDetail(questionnaireIndex)}>
                                                    <h5>{listElement.topic}</h5>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        }.bind(this))
                    }
                </div>
            </div>
        );
    }
}

export default QuestionnaireList;



