import React, { Component } from 'react';
import FormActionBar from '../common/FormActionBar';

class QuestionnaireDetail extends Component {

    constructor(props) {
        super(props);
        this.state  =   {
            questionnaireDetail     :   {}
        };
    }

    componentWillMount() {

        var questionnaireIndex  =   this.props.match.params.questionnaireIndex;

        var questionnaireList   =   localStorage.getItem("questionnaireFormList");
            questionnaireList   =   JSON.parse(questionnaireList);

        console.log("questionnaireList", questionnaireList);

        this.setState({
            questionnaireDetail   :   questionnaireList[questionnaireIndex]
        });
    }

    saveForm() {

        var questionnaireDetail = this.state.questionnaireDetail;

        var storedKey       =   "questionnaire_result_" + questionnaireDetail.topic;

        var storedDetail  =   localStorage.getItem(storedKey);

        if(!storedDetail) {
            storedDetail  =   [];
        }
        else {
            storedDetail  =   JSON.parse(storedDetail);
        }

        storedDetail.push(questionnaireDetail);

        //
        // storedDetail.push({
        //     topic   :   this.state.questionnaireTopic,
        //     form    :   list
        // });

        localStorage.setItem(storedKey, JSON.stringify(storedDetail));

        console.log("saveForm::localStorage", localStorage.getItem(storedKey));

        this.props.history.push("/questionnaires/list");

    }

    handleChange(listIndex, field, value) {

        console.log("handleChange");

        var questionnaireDetail  =   this.state.questionnaireDetail;

        questionnaireDetail.form[listIndex][field]  =   value;

        console.log("questionnaireDetail", questionnaireDetail);

        this.setState({
            questionnaireDetail    :   questionnaireDetail
        });
    }

    render() {
        return (
            <div className="app-pages app-section">
                <div className="container" style={{marginBottom: 40 + "px"}}>

                    <h4>{this.state.questionnaireDetail.topic}</h4>

                    {
                        this.state.questionnaireDetail.form.map(function(listElement, listIndex){
                            return (
                                <div className="product-cart questionnaire-list-item">
                                    <div className="entry">
                                        <div className="cart-title">
                                            <div className="row">
                                                <div className="col s12">
                                                    <h6>Question #{listIndex + 1}</h6>
                                                </div>
                                            </div>

                                            <div className="row row-title">
                                                <div className="col s12">
                                                    <label className="text-label">{listElement.title}</label>
                                                </div>
                                            </div>

                                            {
                                                (listElement.type == "radio") ? (
                                                    <div>
                                                        <form>
                                                            <table>
                                                                {
                                                                    listElement.options.map(function (option, optionIndex) {
                                                                        return (
                                                                            <tr onClick={(e) => this.handleChange(listIndex, "textAnswer", option.text)} >
                                                                                <td className="col-15">
                                                                                    <input type="radio" name={listElement.title} value={option.text} checked={listElement.textAnswer == option.text} /><label>&nbsp;</label>
                                                                                </td>
                                                                                <td className="col-85">
                                                                                    <label className="text-label">{option.text}</label>
                                                                                </td>
                                                                            </tr>
                                                                        )
                                                                    }.bind(this))
                                                                }
                                                            </table>
                                                        </form>
                                                    </div>
                                                ) : (
                                                    <input type="text" value={listElement.textAnswer} placeholder="Your Answer Here..." onChange={(e) => this.handleChange(listIndex, "textAnswer", e.target.value)} />
                                                )
                                            }

                                        </div>
                                    </div>
                                    {
                                        /*
                                         <input type="text" placeholder="Email" />
                                         <select>
                                         <option>Text</option>
                                         <option>Radio Button</option>
                                         </select>
                                         */
                                    }
                                </div>
                            )
                        }.bind(this))
                    }

                    {/*
                     <div className="pages-title">
                     <h3>Login</h3>
                     </div>
                     <form action="#">
                     <input type="email" placeholder="Email" />
                     <input type="password" placeholder="password" />
                     <div><a href="" className="forgot">Forgot Password?</a></div>
                     <div className="chebox">
                     <input type="checkbox" id="checkbox" />
                     <label for="checkbox">Remember me</label>
                     </div>
                     <button className="button">Login</button>
                     <div className="create-account">Not Registered? <a href="">Create an account</a></div>
                     </form>
                     <div className="or">
                     <h5>OR</h5>
                     <button className="button facebook">Login with Facebook</button>
                     <button className="button twitter">Login with Twitter</button>
                     <button className="button google">Login with Google+</button>
                     </div>
                     */}
                </div>
                <FormActionBar saveForm={this.saveForm.bind(this)} label="Save" />
            </div>
        );
    }
}

export default QuestionnaireDetail;



