import React, { Component } from 'react';

class TipsPage extends Component {

    constructor(props) {
        super(props);
        this.state  =   {
            faqList     :   [
                false
            ]
        }
    }

    toggleBox(index) {

        var faqList         =   this.state.faqList;

        for(var i in faqList) {
            faqList[i]  =   false;
        }

        faqList[index]  =   !faqList[index];

        this.setState({
            faqList :   faqList
        });
    }

    render() {
        return (
            <div className="faq app-pages app-section" style={{paddingTop: 0}}>

                <img className="main-content-image" src="assets/image/tips_banner.png" alt="" />

                <div className="container">
                    <br />
                    <h4>Tips</h4>

                    <ul className="collapsible" data-collapsible="accordion" style={{marginTop: "20px"}}>
                        <li>
                            <div className="collapsible-header faq-collapsible" onClick={(e) => this.toggleBox(0)}>
                                How to greetings <i className={"fa " + ((this.state.faqList[0]) ? 'fa-minus' : 'fa-plus')}></i>
                            </div>
                            <div className={"collapsible-body " + (this.state.faqList[0] ? "show" : "hide")} style={{padding: "15px"}}>
                                <div>- Hello, my name is John from ABC Community center.</div>
                                <div>- Hi, nice to meet you. My name is Amy.</div>
                                <div>- Good Morning, my name is Louis from XYZ Primary School.</div>
                            </div>
                        </li>

                        <li>
                            <div className="collapsible-header faq-collapsible" onClick={(e) => this.toggleBox(1)}>
                                How to introduce yourself <i className={"fa " + ((this.state.faqList[1]) ? 'fa-minus' : 'fa-plus')}></i>
                            </div>
                            <div className={"collapsible-body " + (this.state.faqList[1] ? "show" : "hide")} style={{padding: "15px"}}>
                                <div>- I am conducting a project which would like to know more about the tourist in Hong Kong.</div>
                                <div>- We would like to interview different foreign tourists to know why they want to visit Hong Kong.</div>
                            </div>
                        </li>

                        <li>
                            <div className="collapsible-header faq-collapsible" onClick={(e) => this.toggleBox(2)}>
                                Getting their consent <i className={"fa " + ((this.state.faqList[2]) ? 'fa-minus' : 'fa-plus')}></i>
                            </div>
                            <div className={"collapsible-body " + (this.state.faqList[2] ? "show" : "hide")} style={{padding: "15px"}}>
                                <div>- It would be great if you could spend a few minutes having a interview with us.</div>
                                <div>- Would you mind answering a few questions about visting Hong Kong?</div>
                            </div>
                        </li>

                        <li>
                            <div className="collapsible-header faq-collapsible" onClick={(e) => this.toggleBox(3)}>
                                Possible questionnaire questions <i className={"fa " + ((this.state.faqList[3]) ? 'fa-minus' : 'fa-plus')}></i>
                            </div>
                            <div className={"collapsible-body " + (this.state.faqList[3] ? "show" : "hide")} style={{padding: "15px"}}>
                                <div>- Why do you choose to visit Hong Kong?</div>
                                <div>- Is that your first time been to Hong Kong?</div>
                                <div>- How do you feel about Hong Kong?</div>
                                <div>- How many days are you going to spend in Hong Kong?</div>
                            </div>
                        </li>

                        <li>
                            <div className="collapsible-header faq-collapsible" onClick={(e) => this.toggleBox(4)}>
                                Say thank you to the interviewee <i className={"fa " + ((this.state.faqList[4]) ? 'fa-minus' : 'fa-plus')}></i>
                            </div>
                            <div className={"collapsible-body " + (this.state.faqList[4] ? "show" : "hide")} style={{padding: "15px"}}>
                                <div>- Thank you very much for having interview with us.</div>
                                <div>- Thank you very much and have a nice day.</div>
                                <div>- Thank you for your time and enjoy your day.</div>
                            </div>
                        </li>



                    </ul>

                </div>

            </div>
        );
    }
}

export default TipsPage;



