import React, { Component } from 'react';

class PageBar extends Component {

    render() {
        return (
            <div className="page-bar">
                <table>

                    <tr>
                        <td className="col-button-left">
                            <div className={this.props.getCurrentPage() == 1 ? "hide" : ""} onClick={this.props.handlePreviousPage} >
                                <i className="fa fa-chevron-circle-left button-next-page"></i>
                            </div>
                        </td>
                        <td className="col-text">
                            <div className="">
                                <h4>Page {this.props.getCurrentPage()}</h4>
                            </div>
                        </td>
                        <td className="col-button-right">
                            <div className={this.props.getCurrentPage() >= this.props.getLastPage() ? "hide" : ""} onClick={this.props.handleNextPage}>
                                <i className="fa fa-chevron-circle-right button-previous-page"></i>
                            </div>
                        </td>
                    </tr>

                </table>

            </div>
        );
    }
}

export default PageBar;



