import React, { Component } from 'react';
import FormActionBar from '../common/FormActionBar';

class QuestionnaireResultList extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        var questionnaireList   =   localStorage.getItem("questionnaireFormList");

        if(!questionnaireList) {
            questionnaireList   =   [];
        }
        else{
            questionnaireList   =   JSON.parse(questionnaireList);
        }

        console.log("questionnaireList", questionnaireList);

        this.setState({
            questionnaireList   :   questionnaireList
        });
    }

    viewResultDetail(questionnaireIndex) {
        this.props.history.push("/questionnaires/result-detail/" + questionnaireIndex);
    }

    render() {
        return (
            <div className="app-pages app-section">
                <div className="container" style={{marginBottom: 40 + "px"}}>

                    <h4>Your Questionnaire Result</h4>

                    {
                        this.state.questionnaireList.map(function(listElement, questionnaireIndex){
                            return (
                                <div className="product-cart questionnaire-list-item">
                                    <div className="entry">
                                        <div className="cart-title" onClick={(e) => this.viewResultDetail("questionnaire_result_" + listElement.topic)}>
                                            <div className="row">
                                                <div className="col s12">
                                                    <h5>Questionnaire #{questionnaireIndex + 1}</h5>
                                                </div>
                                            </div>
                                            <div className="row">
                                                <div className="col s12" >
                                                    <h5>{listElement.topic}</h5>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        }.bind(this))
                    }
                </div>
            </div>
        );
    }
}

export default QuestionnaireResultList;



