import React, { Component } from 'react';
import $ from 'jquery';

class HomePage extends Component {

    componentDidMount() {

    }

    render() {
        return (
            <div>
                <div className="slider-slick app-pages">
                    <div className="slider-entry">
                        <img src="assets/image/home_banner.png" alt="" />
                    </div>
                </div>

                <div className="courses" style={{paddingBottom: "0px"}}>
                    <div className="container">
                        <div className="app-title" style={{marginTop: "20px"}}>
                            <h4>Lessons</h4>
                            <div className="line"></div>
                        </div>
                        <div className="row">
                            <div className="col s6">
                                <div className="entry bg-color-1">
                                    <h5>Lesson 1</h5>
                                    <p>Learn more about Hong Kong tourist spots in this lesson</p>
                                </div>
                            </div>
                            <div className="col s6">
                                <div className="entry bg-color-2">
                                    <h5>Lesson 2</h5>
                                    <p>Chatting with foreigners are just easy as you think</p>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col s6">
                                <div className="entry bg-color-3">
                                    <h5>Lesson 3</h5>
                                    <p>Want to make your own questionnaire and conduct a interview?</p>
                                </div>
                            </div>
                            <div className="col s6">
                                <div className="entry bg-color-4">
                                    <h5>Lesson 4</h5>
                                    <p>Make good use of the collected data and tell others what you find out</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default HomePage;



