import React, { Component } from 'react';
import PageBar from "./../common/PageBar";
import LessonDetail from "./LessonDetail";

class LessonApp extends Component {

    constructor(props) {
        super(props);
        this.state  =   {
            lessonId    :   "",
            page        :   (this.props.match.params.page) ? this.props.match.params.page : 1
        };
    }

    getCurrentPage() {
        return (this.props.match.params.page == 1) ? 1 : this.state.page;
    }

    getLastPage() {

        switch (Number(this.props.match.params.id)) {
            case 1:
                return 7;
            case 2:
                return 6;
            case 3:
                return 6;
            case 4:
                return 7;
        }
    }

    goToPage(direction) {

        var page    =   Number(this.getCurrentPage());

        if(direction === "previous") {
            page    -=  1;
        }
        else if(direction === "next") {
            page    +=  1;
        }

        this.setState({
            page    :   page
        });

        this.props.history.push("/lessons/" + this.props.match.params.id + "/" + page);

    }

    render() {
        return (
            <div className="app-pages">
                <LessonDetail lessonId={this.props.match.params.id} getCurrentPage={this.getCurrentPage.bind(this)} />
                <PageBar getLastPage={this.getLastPage.bind(this)} getCurrentPage={this.getCurrentPage.bind(this)} handlePreviousPage={() => this.goToPage("previous")} handleNextPage={() => this.goToPage("next")} />
            </div>
        );
    }
}

export default LessonApp;



