import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom';
import Navbar from './app/components/common/Navbar';
import LoginPage from "./app/components/login/LoginPage";
import AboutPage from "./app/components/about/AboutPage";
import HomePage from "./app/components/home/HomePage";
import LessonApp from "./app/components/lessons/LessonApp";
import QuestionnaireGeneratorApp from "./app/components/tools/QuestionnaireGeneratorApp";
import QuestionnaireList from "./app/components/tools/QuestionnaireList";
import QuestionnaireDetail from "./app/components/tools/QuestionnaireDetail";
import QuestionnaireResultList from "./app/components/tools/QuestionnaireResultList";
import QuestionnaireResultDetail from "./app/components/tools/QuestionnaireResultDetail";
import TipsPage from "./app/components/quick-tip/TipsPage";
import ContactPage from "./app/components/contact/ContactPage";

class App extends Component {
    render() {
        return (
            <div className="">
                <Router >
                  <div>
                      <Navbar />
                      <Route exact path="/" component={HomePage}/>
                      <Route path="/login" component={LoginPage}/>
                      <Route path="/about" component={AboutPage} />
                      <Route path="/lessons/:id/:page" component={LessonApp}/>
                      <Route path="/questionnaires/create" component={QuestionnaireGeneratorApp} />
                      <Route path="/questionnaires/list" component={QuestionnaireList} />
                      <Route path="/questionnaires/detail/:questionnaireIndex" component={QuestionnaireDetail} />
                      <Route path="/questionnaires/result" component={QuestionnaireResultList} />
                      <Route path="/questionnaires/result-detail/:questionnaireKey" component={QuestionnaireResultDetail} />
                      <Route path="/tips" component={TipsPage} />
                      <Route path="/contact" component={ContactPage} />
                  </div>
                </Router>
            </div>
        );
    }
}

export default App;



