import React, { Component } from 'react';

import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom';

class Navbar extends Component {
    render() {
        return (
            <div>
                <div className="navbar">
                    <div className="container">
                        <div className="panel-control-right">
                            <a href="#" data-activates="slide-out-right" className="sidenav-control-right"><i className="fa fa-bars"></i></a>
                        </div>
                        <div className="site-title">
                            <Link to="/"><h1>Fun Summer</h1></Link>
                        </div>
                    </div>
                </div>

                <div className="panel-control-right">
                    <ul id="slide-out-right" className="side-nav collapsible"  data-collapsible="accordion">
                        <li>
                            <Link to="/"><i className="fa fa-home"></i>Home</Link>
                        </li>

                        <li>
                            <Link to="/about"><i className="fa fa-graduation-cap"></i>About the course</Link>
                        </li>

                        <li>
                            <div className="collapsible-header"><i className="fa fa-book"></i>Lessons <span><i className="fa fa-chevron-right"></i></span></div>
                            <div className="collapsible-body">
                                <ul className="side-nav-panel">
                                    <li><Link to="/lessons/1/1">1. Tourist spots in Hong Kong</Link></li>
                                    <li><Link to="/lessons/2/1">2. Chat with foreign tourist</Link></li>
                                    <li><Link to="/lessons/3/1">3. Design a questionnaire</Link></li>
                                    <li><Link to="/lessons/4/1">4. Analyst and presentation skills</Link></li>
                                </ul>
                            </div>
                        </li>
                        <li>
                            <div className="collapsible-header"><i className="fa fa-files-o"></i>Tools <span><i className="fa fa-chevron-right"></i></span></div>
                            <div className="collapsible-body">
                                <ul className="side-nav-panel">
                                    <li><Link to="/questionnaires/create">Questionnaire Generator</Link></li>
                                    <li><Link to="/questionnaires/list">My Questionnaires</Link></li>
                                    <li><Link to="/questionnaires/result">Questionnaire Results</Link></li>
                                </ul>
                            </div>
                        </li>
                        <li>
                            <Link to="/tips"><i className="fa fa-question-circle"></i>Quick Tips</Link>
                        </li>
                        <li>
                            <Link to="/contact"><i className="fa fa-envelope"></i>Contact Us</Link>
                        </li>
                    </ul>
                </div>

            </div>
        );
    }
}

export default Navbar;



