import React, { Component } from 'react';

class AboutPage extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="app-pages app-section" style={{paddingTop: 0}}>

                <img className="main-content-image" src="assets/image/about_banner.png" alt="" />

                <div className="container" style={{marginBottom: 40 + "px"}}>
                    <br />
                    <h4>About this course</h4>
                    <br />
                    <h6>
                        You will complete 4 lessons which related to interview foreign tourist. The content of the lessons includes learning the terms of the tourist spots, the skills to make conversation with foreign tourist, the technique of making questionnaires the analytical and presentation skills.
                    </h6>
                    <br />
                    <h6>
                        You will also have the opportunity interview the foreign tourist in Hong Kong tourist spot after the 4 lessons are completed.
                    </h6>
                </div>

            </div>
        );
    }
}

export default AboutPage;



